@import url(https://fonts.googleapis.com/css?family=Kalam&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 500px) {
    .home {
        padding: 100px 40px;
      
    }
}
.avatar {
  display:block;
  margin-left: auto;
  margin-right: auto;
  border-radius: 5%;
  width: 35%;
}


.centered {
  font-family: Roboto;
  font-size: 32px;
  font-weight: bold;
  position: absolute;
  letter-spacing: 1px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color:white;
  text-shadow: 1px 1px #4b4444;
}
.background {
  background-image: url=("https://i.imgur.com/Br17b4U.jpg")''
}
.layout {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.layout-children {
    flex: 2 1;
    padding: 100px;
}

@media (max-width: 500px) {
    .layout {
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .layout-children {
        width: 80vw;
        padding: 0px;
        margin: 0px;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
.paragraph {
  margin: auto;
  width: 65%;
  padding: 10px;

}
footer {
  flex: 1 1;
  position: fixed;
  width: 100%;
  background-color: white;
  z-index: 1;
  bottom: 0;
  margin-right: 50%;
  padding: auto;
}

nav {
    flex: 1 1;
    position: fixed;
    width: 100%;
    background-color: white;
    z-index: 1;
    top: 0;
    left: 0;
}

.nav {
    display: flex;
    padding: 20px 0;
    margin-left: -2px;
    margin-right: -20px;
    margin-top: -2px;
    box-shadow: 1px 1px 1px .3px #b6b4b4;
    width: 100%;
}

.logo {
    font-family: 'Alata', sans-serif;
    text-align: center;
    font-weight: bold;
    font-size: 28px;
    letter-spacing: 0px;
    flex: 1 1;
    max-width: 30vw;
    transition: 250ms all;
}

.logo:hover {
    transform: scale(1.004);
}

.links {
    flex: 2 1;
    max-width: 75vw;
    align-items: left;
    font-size: 18px;
    display: flex;
}

.link {
    flex: 1 1;
}

a {
    text-decoration: none;
    color: black;
}

@media (max-width: 500px) {
    nav {
        width: 98%;
    }
    .logo {
        max-width: 50%;
    }
    .links {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        max-width: 50vw;
    }
    .link {
        padding: 3px;
    }
}

footer {
  display: block;
  flex: 1 1;
  position: fixed;
  width: 100%;
  background-color: white;
  z-index: 5;
  bottom: 0;
 text-align: center;
  padding: auto;
}

